import {createRouter, createMemoryHistory} from 'vue-router';
import Storage from '@/utils/DeviceStorage';

const routes = [
    {
        path: '/',
        redirect: '/Home',
    },
    {
        path:"/Home",
        name:'Home',
        meta:{
            requireAuth: true
        },
        component: ()=> import('../main/Home/home')
    },
    {
        path:"/Login",
        name:"Login",
        component: ()=>import('../main/Login/login')
    }
   
]



const router = createRouter({
    history: createMemoryHistory(),
    routes
})


router.beforeEach(function (to,from,next){
const isLogin = Storage.get('yuelian');
    if (to.path == "/Login") {
        if(isLogin) {
            next("/Home")
        }else{
            next()
        }
    }else{
        if (to.meta.requireAuth && isLogin) {
            next();
        } else {
            next("/Login");
        }
    }
})

export default router